export function collectionListUtils({ element }) {
    const { settings } = element;
    const { column } = element.settings;
    const colDesktop = column.desktop.value;
    const colTablet = column?.tablet?.value ?? colDesktop;
    const colMobile = column?.mobile?.value ?? colTablet;
    const gapDesktop = settings.colGap.desktop.value;
    const gapTablet = settings.colGap?.tablet?.value ?? gapDesktop;
    const gapMobile = settings.colGap?.mobile?.value ?? gapTablet;
    const layoutClass = `xb-collection-list-${settings.$layout_type}`;
    const autoplayAttr = settings.autoplayEnabled === 'yes' ? { 'xo-autoplay': settings.autoplayDelay.value * 1000 } : {};
    return {
        colDesktop,
        colTablet,
        colMobile,
        layoutClass,
        gapDesktop,
        gapTablet,
        gapMobile,
        autoplayAttr,
    };
}
